// -*- scope: emm; createEmpty: true -*-

/*doc
---
title: Auto-Import Badges
name: badges_10_auto-import
parent: badges_04_status
---

`.auto.import.status` should always be combined with the following statuses: `active`, `inactive`.

```htmlexample
<span class="status-badge auto.import.status.active"></span>
<span class="status-badge auto.import.status.inactive"></span>
```
*/

$auto-import-status-badges-defs: (
  active:   (background: $badge-green-var, icon: $fa-var-play),
  inactive: (background: $badge-grey-var,  icon: $fa-var-pause)
);

@mixin auto-import-status-badge($state) {
  $background: map-get(map-get($auto-import-status-badges-defs, $state), background);
  $icon: map-get(map-get($auto-import-status-badges-defs, $state), icon);
  @include status-badge($background, $icon);
}

@each $state, $values in $auto-import-status-badges-defs {
  .auto\.import\.status\.#{$state} {
    @include auto-import-status-badge($state);
  }
}

