// -*- scope: emm; createEmpty: true -*-

/*doc
---
title: Building block Badges
name: badges_11_building-block
parent: badges_04_status
---

`.building.block.status` should always be combined with the following statuses: `in-use`, `not-in-use`.

```htmlexample
<span class="status-badge building.block.status.in-use"></span>
<span class="status-badge building.block.status.not-in-use"></span>
```
*/

$building-block-status-badges-defs: (
  in-use:     (background: $badge-darkest-blue-var,   icon: $fa-var-cogs),
  not-in-use: (background: $stat-light-grey,          icon: $fa-var-cogs)
);

@mixin building-block-status-badge($state) {
  $background: map-get(map-get($building-block-status-badges-defs, $state), background);
  $icon: map-get(map-get($building-block-status-badges-defs, $state), icon);
  @include status-badge($background, $icon);
}

@each $state, $values in $building-block-status-badges-defs {
  .building\.block\.status\.#{$state} {
    @include building-block-status-badge($state);
  }
}

